import React, { useState } from 'react';
import { Button, Form, Row, Col, Container, Alert } from 'react-bootstrap'


const TestVideo = ({ token, setUserPageState, isGenerating, setIsGenerating,
    imageType, setImageType, imageData, setImageData, videoData, setVideoData, checked, setChecked, message, setMessage }) => {


    

    let handleImage = (e) => {
        if(!e.target.files[0]) return;
        

        var reader = new FileReader();
        reader.onload = function (frEvent) {

            // var image = new Image();
            // image.onload = function (imageEvent) {

            //     // Resize the image
            //     var canvas = document.createElement('canvas'),
            //         y_offset = 0,
            //         x_offset = 0,
            //         set_width = 256,
            //         set_height = 256,
            //         img_width = image.width,
            //         img_height = image.height;

            //     if (img_width > img_height) {
            //         x_offset = (img_width - img_height) / 2
            //     } else {
            //         y_offset = (img_height - img_width) / 2
            //     }

            //     canvas.width = set_width;
            //     canvas.height = set_height;
            //     canvas.getContext('2d').drawImage(image, x_offset, y_offset, img_width - x_offset * 2, img_height - y_offset * 2,
            //         0, 0, set_width, set_height);
            //     var dataUrl = canvas.toDataURL('image/jpeg');
            //     setImageData(dataUrl)

            // }
            // image.src = frEvent.target.result;
        
            setImageData(frEvent.target.result);
            
        }

        setImageType(e.target.files[0].type)
        reader.readAsDataURL(e.target.files[0]);
    }

    let handleSubmit = async (e) => {
        setIsGenerating(true)
        e.preventDefault();
        var start = window.performance.now();
        setVideoData(false)
        setMessage(false)
        let res = await fetch(`https://shoevideo.azurewebsites.net/api/test_video`, {
            method: "POST",
            body: JSON.stringify({
                'image_data': imageData.split(",", 2)[1],
                'image_type': imageType,
                'is_save': checked

            }),
            headers: {
                'Content-Type': 'application/json',
                'Accept': 'application/json',
                'x-functions-key': token
            },
        })

        let resData = await res.json();
        if (res.ok) {
            setVideoData(resData['result_url'])

            var end = window.performance.now();

            setMessage({"status": "success", "message": "Video successfully generated in " +((end - start) / 1000).toFixed(2) + " seconds."});
            setIsGenerating(false)
        }
        else {


            if(res.status === 404){
                setMessage({"status": "danger", "message": resData['detail'], "time": ""});
            }
            else{
                setMessage({"status": "danger", "message": "Error code: "+ (res.status).toString(), "time": ""});

            }

            setIsGenerating(false)
        }
    }

    return (
        <Container style={{}}>
            <Row className="mt-4">

                <Col xs lg="2" >
                    <Button variant="warning" size="sm" className='' onClick={() => { setUserPageState(""); }}>
                        Back
                    </Button>
                </Col>
                <Col xs lg="8">


                    {/* <LoadingModal show={loadingShow} text={'Generating video'} /> */}
                    <Form onSubmit={handleSubmit} className='mb-4'>
                        <Row className='mb-1'>
                            <Col>
                                <Form.Group>
                                    <Form.Control type="file" accept="image/*" onChange={handleImage} />
                                </Form.Group>
                            </Col>

                            <Col xs lg="4">
                                <Button variant="primary" type="submit" disabled={isGenerating} style={{width: "160px"}}>
                                    {isGenerating ? 'Generating video...' : 'Submit'}

                                </Button>
                            </Col>
                            <Col xs lg="1">

                            </Col>

                        </Row>
                        <Row>
                            <Form.Group>

                                <div className="form-check form-switch">
                                    <Form.Control className="form-check-input" type="checkbox" checked={checked}
                                        onChange={(e) => setChecked(e.currentTarget.checked)} />
                                    <label className="form-check-label text-muted">Add to results</label>
                                </div>
                            </Form.Group>
                        </Row>

                    </Form>


                    <Row style={{ height: '420px' }}>
                        <Col xs lg="6">
                            <img src={imageData} style={{ width: "90%" }} alt=""/>

                        </Col>
                        <Col xs lg="6">
                            {videoData &&
                                <video width="90%" autoPlay loop>
                                    <source src={videoData} type="video/mp4"></source>
                                </video>
                            }
                        </Col>
                    </Row>

                    {message &&

                        <Alert  variant={message['status']}>
                            {message['message']}
                        </Alert>
                    }


                </Col>
            </Row>
        </Container>
    );
};

export default TestVideo;
