import React, { useState } from 'react'
import { Button } from 'react-bootstrap'
import ConfirmModal from './ConfirmModal'


const PreviewFeed = ({ setCreateFeedState, setAlertState, feedData, setData, token }) => {
    
    const [modalShow, setModalShow] = useState(false)
    const openModal = () => setModalShow(true)
    const closeModal = () => setModalShow(false)
    
    let confirmGenerate = (e) => {
        openModal();
    }

    let handleGenerate = async () => {  

        setCreateFeedState("loading")
        let res = await fetch(`https://shoevideo.azurewebsites.net/api/save_video_props_feed`, {
            method: "POST",
            body: JSON.stringify({
                'feed_id': feedData['feed_id'],
            }),
            headers: {
                'Content-Type': 'application/json',
                'Accept': 'application/json',
                'x-functions-key': token


            },
        })
    
        let resData = await res.json();
        if (res.ok) {
            let res2 = await fetch(`https://shoevideo.azurewebsites.net/api/generate_video_feed`, {
                method: "POST",
                body: JSON.stringify({
                    'video_ids': resData['video_ids'],
                }),
                headers: {
                    'Content-Type': 'application/json',
                    'Accept': 'application/json',
                },
            })
        
            let res2Data = await res2.json();
            if (res2.ok) {
                setCreateFeedState("success")

                return


            }
            else {
            }
        }
        else {
        }

    }

    return (

        <div>
            <video width="320" height="240" autoPlay loop>
                <source src={feedData['previewVideoUrl']} type="video/mp4"></source>
            </video>
            <div>

            {/* <img width="320" src={feedData['previewVideoUrl']} /> */}
            </div>

            
            {/* <Button variant="primary" onClick={confirmGenerate}>Start Generating Videos</Button> */}

            <ConfirmModal show={modalShow} title="Save Feed" text="Are you sure to start generating videos from this feed?"
                buttonText="Yes" handleClose={closeModal} handleConfirm={handleGenerate} />
        </div>
        )
}

export default PreviewFeed
