import React, { useState } from 'react'
import { Form, Row, Col, Table, Button} from 'react-bootstrap'
import ConfirmModal from './ConfirmModal'

const ArrangeFeed = ({ setCreateFeedState, setAlertState, feedData, setData, token }) => {


    const [ImgUrl, setImgUrl] = useState(false)
    const [Title, setTitle] = useState(false)
    const [Body, setBody] = useState(false)
    const [WebUrl, setWebUrl] = useState(false)

    const colOptions = [{"col":"Image URL", "set": setImgUrl, "get": ImgUrl}, 
                        {"col": "Title", "set": setTitle, "get": Title}, 
                        {"col": "Body", "set": setBody, "get": Body}, 
                        {"col": "Website URL", "set": setWebUrl, "get": WebUrl}];
    

    const [modalShow, setModalShow] = useState(false)
    const openModal = () => setModalShow(true)
    const closeModal = () => setModalShow(false)
    let handleSaveClick = (e) => {

        if(ImgUrl && Title && Body && WebUrl){
            openModal(); 
        }
        else{
            setAlertState({ show: true, text: "Please fill all of the fields first." })
        }
        e.preventDefault();
    }
    let handleSubmit = async () => {
        setCreateFeedState("loading")
        setAlertState({ show: false, text: "" })
        let res = await fetch(`https://shoevideo.azurewebsites.net/api/create_feed`, {
            method: "POST",
            body: JSON.stringify({
                'user_id': 1,
                'feed_name': feedData.feed_name,
                'feed_url': feedData.feed_url,
                'img_url_col': ImgUrl,
                'title_col': Title,
                'site_url_col': WebUrl,
                'body_col': Body,

            }),
            headers: {
                'Content-Type': 'application/json',
                'Accept': 'application/json',
                'x-functions-key': token
            },
        })

        let resData = await res.json();
        if (res.ok) {

            // data["first_rows"] = feedData.feed_data.data;
            feedData["title_col"] = resData["title_col"]
            feedData["site_url_col"] = resData["site_url_col"]
            feedData["img_url_col"] = resData["img_url_col"]
            feedData["body_col"] = resData["body_col"]
            feedData["feed_id"] = resData["feed_id"]
            
            setData(feedData)
            setCreateFeedState("arrangeVisuals");
        }
        else {
            setCreateFeedState("checkURL");
            setAlertState({ show: true, text: resData['detail'] })
        }
    }
    return (
        <div>
            {/* <Form onSubmit={e => {openModal(); e.preventDefault();}}> */}
            <Form onSubmit={handleSaveClick}>

                <Row>
                    {colOptions.map((colOption, i) =>
                        <React.Fragment key={i}>
                            <Col>
                                <Form.Group as={Row} className="mb-3">
                                    <Form.Label column sm={4} className='text-end fw-bold'>
                                        {colOption.col}
                                    </Form.Label>
                                    <Col sm={8}>

                                        {/* <Form.Select aria-label="Default select example" onChange={e => setColMatches(state => (state[colOption] = e.target.value))}> */}
                                        {/* <Form.Select aria-label="Default select example"  value={colOption.get} onChange={(e)=>{colOption.set(e)}}> */}
                                        <Form.Select aria-label="Default select example" value={colOption.get}   onChange={(e)=>{colOption.set(e.target.value)}}>
                                            <option disabled value={false}>Select..</option>
                                            
                                            {feedData.col_names.map((colName, i) =>

                                                <option key={i} value={colName}>{colName}</option>

                                            )}
                                        </Form.Select>
                                    </Col>
                                </Form.Group>
                            </Col>

                            {(i % 2 === 1) &&
                                <div className="w-100"></div>
                            }
                        </React.Fragment>
                    )}
                </Row>
                <Button variant="primary" type='submit'>Save</Button>

            </Form>
            <div className='mt-4'>
                <Table striped bordered hover size="sm">
                    <thead>
                        <tr>
                            <th>#</th>
                            {feedData.col_names.map((colName, i) =>
                                <th key={i}>{colName}</th>

                            )}
                        </tr>
                    </thead>
                    <tbody>

                        {feedData.feed_data.data.map((feed, i) =>
                            <tr key={i}>
                                {/* 50 den buyuk karakterli satırlar için hepsini yazdırma */}
                                {Object.values(feed).map((element, i) =>
                                    <td key={i}>

                                        {element.toString().length < 50 ? element : element.toString().substring(0, 50) + '...'}</td>
                                )}
                            </tr>
                        )}

                    </tbody>
                </Table>
            </div>
            <ConfirmModal show={modalShow} title="Save Feed" text="Are you sure to create a new feed?"
                buttonText="Yes" handleClose={closeModal} handleConfirm={handleSubmit} />
        </div >
    )
}

export default ArrangeFeed
