export const  getCookie = (name) => {
    let cookieValue = null;
    if (document.cookie && document.cookie !== '') {
        const cookies = document.cookie.split(';');
        for (let i = 0; i < cookies.length; i++) {
            const cookie = cookies[i].trim();
            // Does this cookie string begin with the name we want?
            if (cookie.substring(0, name.length + 1) === (name + '=')) {
                cookieValue = decodeURIComponent(cookie.substring(name.length + 1));
                break;
            }
        }
    }
    return cookieValue;
}



export const getPreviewVideo = async (feedData, token) => {

    let res = await fetch(`https://shoevideo.azurewebsites.net/api/save_video_props`, {
        method: "POST",
        body: JSON.stringify({
            'user_id': 1,
            'feed_id': feedData['feed_id'],
            'img_url': feedData['feed_data']['data'][0][feedData['img_url_col']],
            'title': feedData['feed_data']['data'][0][feedData['title_col']],
            'body': feedData['feed_data']['data'][0][feedData['body_col']],
            'site_url': feedData['feed_data']['data'][0][feedData['site_url_col']],



        }),
        headers: {
            'Content-Type': 'application/json',
            'Accept': 'application/json',
            'x-functions-key': token
        },
    })

    let resData = await res.json();
    if (res.ok) {
        let res2 = await fetch(`https://shoevideo.azurewebsites.net/api/generate_video`, {
            method: "POST",
            body: JSON.stringify({
                'video_id': resData['video_id'],
            }),
            headers: {
                'Content-Type': 'application/json',
                'Accept': 'application/json',
                'x-functions-key': token

            },
        })
    
        let res2Data = await res2.json();
        if (res2.ok) {
            return res2Data['result_url']
        }
        else {
        }
    }
    else {
    }
    
}


