import React from 'react'
import { Container, Row, Col } from 'react-bootstrap'

const MainPage = () => {
    return (
        <div>
            <Container>
                <Row className="justify-content-md-center">
                    <Col lg="8">
                        <div className='mt-2 fs-4'>
                            <strong >Main Page</strong> <span></span>
                        </div>
                    </Col>
                </Row>

            </Container>
        </div>
    )
}

export default MainPage
