import React, { useState, useEffect} from 'react'
import { Container, Alert, Row, Col, ProgressBar } from 'react-bootstrap'
import CreateFeedForm from '../components/CreateFeedForm'
import ArrangeFeed from '../components/ArrangeFeed'
import Loading from '../components/Loading'
import PreviewFeed from '../components/PreviewFeed'
import ArrangeVisuals from '../components/ArrangeVisuals'
const CreateFeedPage = ({token}) => {
    const [CreateFeedState, setCreateFeedState] = useState("checkURL")

    const [AlertState, setAlertState] = useState({ show: false, text: "" })
    const [Data, setData] = useState({})
    const [Progress, setProgress] = useState({"percent": 10, "label": "1/5"})
    
    useEffect(() => {
        switch (CreateFeedState) {
            case "checkURL":
                setProgress({"percent": 10, "label": "1/5"});
                break;
            case "arrangeFeed":
                setProgress({"percent": 35, "label": "2/5"});
                break;
            case "arrangeVisuals":
                setProgress({"percent": 60, "label": "3/5"});
                break;
            case "preview":
                setProgress({"percent": 85, "label": "4/5"});
                break;
            case "success":
                setProgress({"percent": 100, "label": "5/5"});
                break;
            case "loading":
                break;
            default:
                return ""
        }

        return () => {
        }
    }, [CreateFeedState])

    const header = (CreateFeedState) => {
        switch (CreateFeedState) {
            case "checkURL":
                return "Provide a URL"
            case "arrangeFeed":
                return Data.feed_name + " - Match the columns"
            case "arrangeVisuals":
                return Data.feed_name + " - Arrange Visuals"
            case "preview":
                return Data.feed_name + " - Video Preview"
            case "loading":
                return ""
            default:
                return ""
        }
    }


    const main = (CreateFeedState) => {
        switch (CreateFeedState) {
            case "checkURL":
                return <CreateFeedForm setCreateFeedState={setCreateFeedState}
                    setAlertState={setAlertState} setData={setData} token={token}/>
            case "arrangeFeed":
                return <ArrangeFeed setCreateFeedState={setCreateFeedState} 
                setAlertState={setAlertState} feedData={Data} setData={setData} token={token}/>

            case "arrangeVisuals":
                return <ArrangeVisuals setCreateFeedState={setCreateFeedState} 
                setAlertState={setAlertState} feedData={Data} setData={setData} token={token}/>
            case "preview":
                return <PreviewFeed setCreateFeedState={setCreateFeedState} 
                setAlertState={setAlertState} feedData={Data} token={token}/>

            case "success":
                return <div className='fs-5'>
                        Videos successfully generated.                  
                    </div>
            case "loading":
                return <Loading width={300} height={200} />
            default:
                return <CreateFeedForm setCreateFeedState={setCreateFeedState}
                    setAlertState={setAlertState} setData={setData} token={token}/>
        }
    }

    return (
        <div>
            <Container>
                <Row className="justify-content-md-center">
                    <Col lg="8">
                        <div className='mt-2 fs-4'>
                            <strong >Create a new feed:</strong> <span>{header(CreateFeedState)}</span>
                        </div>
                        <ProgressBar className='fs-5 fw-bold' style={{height: '25px'}} striped variant="info" now={Progress.percent} label={Progress.label} />
                        <Alert className='mt-2' variant='danger' style={{ visibility: AlertState.show ? 'visible' : 'hidden', height: "50px" }}>
                            {AlertState.text}
                        </Alert>
                        {main(CreateFeedState)}
                    </Col>
                </Row>

            </Container>
        </div>
    )
}

export default CreateFeedPage
