import React, { useState, useEffect } from 'react'
import { Button, Row, Col, Container } from 'react-bootstrap'
import LoadingModal from '../components/LoadingModal'
const CompareTests = ({ token, setUserPageState }) => {

    const [videosData, setVideosData] = useState([])
    const [loadingShow, setLoadingShow] = useState(false);

    useEffect(() => {
        async function getUser() {
            setLoadingShow(true)

            let res = await fetch(`https://shoevideo.azurewebsites.net/api/get_test_videos`, {
                method: "GET",
                headers: {
                    'x-functions-key': token
                },
            })

            let resData = await res.json();
            if (res.ok) {
                setVideosData(resData['tests'])
                setLoadingShow(false)

            }
            else {
                setLoadingShow(false)

            }
        }
        getUser();

    }, [token]);


    return (
        <Container>
            <Button variant="warning" size="sm" className='mt-2' onClick={() => { setUserPageState(""); }}>
                Back
            </Button>


            <Row className="mt-4">

                <LoadingModal show={loadingShow} text={'Fetching test pairs'} />
                <Row style={{ height: '320px' }}>

                    {videosData.map((item, i) =>
                        <React.Fragment key={i}>
                        
                            <Col xs lg="6">
                                <Row className='gx-0'>
                                    <div>{item.created_at}</div>
                                    <Col xs lg="6">
                                        <img src={item.source_url} style={{ width: "100%" }} alt=""/>

                                    </Col>
                                    <Col xs lg="6">
                                        <video width="100%" autoPlay loop>
                                            <source src={item.result_url} type="video/mp4"></source>
                                        </video>
                                    </Col>
                                </Row>
                            </Col>
                            {(i % 2 === 1) &&
                                <div className="w-100"></div>
                            }
                        </React.Fragment>
                        

                    )}

                </Row>
            </Row>
        </Container>
    );

}

export default CompareTests