import './App.css';
import React, { useState } from 'react'

import {
  // BrowserRouter as Router,
  HashRouter as Router,
  Routes,
  Route
}  from "react-router-dom";
import Header from './components/Header';
import MainPage from './pages/MainPage';
import CreateVideosPage from './pages/CreateVideosPage';
import CreateFeedPage from './pages/CreateFeedPage';
import LoginPage from './pages/LoginPage';
import UserPage from './pages/UserPage';


const getToken = () => {
  const tokenString = sessionStorage.getItem('token');
  return tokenString
};

const getEmail = () => {
  const emailString = sessionStorage.getItem('email');
  return emailString
};

const saveToken = (userToken, email) => {
  sessionStorage.setItem('token', userToken);
  sessionStorage.setItem('email', email);

};

const logout = () => {
  sessionStorage.removeItem('token');
  window.location.reload();

};

function App() {
  const [userPageState, setUserPageState] = useState("")

  const [email, setEmail] = useState('')
  const [password, setPassword] = useState('')
  const [token, setToken] = useState(getToken())

  const [isGenerating, setIsGenerating] = useState(false)


  if(!token){
    return (
      <LoginPage email={email} password={password} setEmail={setEmail} setPassword={setPassword} saveToken={saveToken} setToken={setToken}/>
    )
  }

  else{
  return (
  <>    
    <Router>
      <Header email={getEmail()} logout={logout} setUserPageState={setUserPageState}/>
      <Routes >
        <Route path="" element={ <MainPage/> } />
        <Route path="createFeed" element={<CreateFeedPage token={getToken()}/>} />

        <Route path="createVideos" element={<CreateVideosPage/>}/>
        <Route path="user" element={<UserPage token={getToken()} email={getEmail()} 
        setUserPageState={setUserPageState} userPageState={userPageState} isGenerating={isGenerating} setIsGenerating={setIsGenerating}/>} />

      </Routes>
    </Router>

    {isGenerating &&
      <div className='p-1 btn btn-outline-primary disabled m-2' style={{position: "fixed", bottom: "0", right: "0"}}>

        Generating video...
        </div>
    }
    </>
  );
}
}

export default App;
