import React, { useState } from 'react';
import { Button, Form, Row, Col, Container } from 'react-bootstrap'
import LoadingModal from '../components/LoadingModal'

const LoginPage = ({ email, password, setEmail, setPassword, saveToken, setToken }) => {

    const [loadingShow, setLoadingShow] = useState(false);

    let handleSubmit = async (e) => {

        setLoadingShow(true)

        e.preventDefault();

        let res = await fetch(`https://shoevideo.azurewebsites.net/api/authenticate`, {
            method: "POST",
            body: JSON.stringify({
                'email': email,
                'password': password,

            }),
            headers: {
                'Content-Type': 'application/json',
                'Accept': 'application/json',
            },
        })

        let resData = await res.json();

        if (res.ok) {

            saveToken(resData['key'], email)
            setToken(resData['key'])
            setLoadingShow(false)

        }
        else {
            setLoadingShow(false)
        }
    }
    return (
        <div>
            <LoadingModal show={loadingShow} text={'Logging in...'} />

            <Container>
                <Row className="justify-content-md-center">
                    <Col lg="5">
                        <Form onSubmit={handleSubmit} className='mt-4'>
                            <Form.Group className="mb-3" controlId="formBasicEmail">
                                <Form.Label>Email address</Form.Label>
                                <Form.Control type="email" placeholder="Enter email" value={email} onInput={e => setEmail(e.target.value)} />
                            </Form.Group>

                            <Form.Group className="mb-3" controlId="formBasicPassword">
                                <Form.Label>Password</Form.Label>
                                <Form.Control type="password" placeholder="Password" value={password} onInput={e => setPassword(e.target.value)} />
                            </Form.Group>

                            <Button variant="primary" type="submit">
                                Submit
                            </Button>
                        </Form>
                    </Col>
                </Row>

            </Container>
        </div >

    );
};

export default LoginPage;
