import React from 'react'
import { Navbar, Nav, NavDropdown, Container} from 'react-bootstrap'
import { Link } from "react-router-dom";


const Header = ({email, logout, setUserPageState}) => {
    return (
        <Navbar collapseOnSelect expand="lg" bg="dark" variant="dark">
            <Container>
                <Navbar.Brand as={Link} to="/">AdVideoMaker</Navbar.Brand>
                <Navbar.Toggle aria-controls="responsive-navbar-nav" />
                <Navbar.Collapse id="responsive-navbar-nav">
                    <Nav className="me-auto">
                        
                        <Nav.Link as={Link} to="/">Home</Nav.Link>
                        <Nav.Link as={Link} to="/createFeed">Create Feed</Nav.Link>
                        {/* <Nav.Link as={Link} to="/createVideos">Create Videos</Nav.Link> */}

                        {/* <NavDropdown title="Dropdown" id="collasible-nav-dropdown">
                            <NavDropdown.Item href="#action/3.1">Action</NavDropdown.Item>
                            <NavDropdown.Item href="#action/3.2">Another action</NavDropdown.Item>
                            <NavDropdown.Item href="#action/3.3">Something</NavDropdown.Item>
                            <NavDropdown.Divider />
                            <NavDropdown.Item href="#action/3.4">Separated link</NavDropdown.Item>
                        </NavDropdown> */}
                    </Nav>
                    {/* <Nav>
                        <Nav.Link as={Link} to="/" eventKey={1}>PlaceHolder1</Nav.Link>
                        <Nav.Link eventKey={2} as={Link} to="/">
                            PlaceHolder2
                        </Nav.Link>
                    </Nav> */}
                    <Nav>
                        <Nav.Link onClick={() => {setUserPageState("")}} as={Link} to="/user">
                            {email}
                        </Nav.Link>
                        <Nav.Link onClick={logout}>
                            Log Out
                        </Nav.Link>
                    </Nav>
                </Navbar.Collapse>
            </Container>
        </Navbar>
    )
}
export default Header