import React, { useEffect, useState } from 'react';
import TestVideo from '../components/TestVideo';
import CompareTests from '../components/CompareTests';
import { Button, Row, Col } from 'react-bootstrap'
import LoadingModal from '../components/LoadingModal'


const UserPage = ({ token, email, userPageState, setUserPageState, isGenerating, setIsGenerating }) => {

    // const [userPageState, setUserPageState] = useState("")
    const [userType, setUserType] = useState("")
    const [loadingShow, setLoadingShow] = useState(false);
    
    const [imageType, setImageType] = useState("")
    const [imageData, setImageData] = useState("")
    const [videoData, setVideoData] = useState(false)
    const [checked, setChecked] = useState(false);
    const [message, setMessage] = useState(false)

    const main = (userPageState) => {
        switch (userPageState) {

            
            case "compareTests":
                return <CompareTests token={token} setUserPageState={setUserPageState} className='mt-4' />

            case "testVideo":
                return <TestVideo token={token} setUserPageState={setUserPageState} className='mt-4'
                isGenerating={isGenerating} setIsGenerating={setIsGenerating} setImageType={setImageType} imageType={imageType} 
                imageData={imageData} setImageData={setImageData} videoData={videoData} setVideoData={setVideoData}
                checked={checked} setChecked={setChecked} message={message} setMessage={setMessage}/>
            default:
                return <div className='mt-5'>
                    <Row className="justify-content-md-center" md="auto"    >
                        <Col>
                            {userType === "admin" &&
                                <>
                                    <Button variant="primary" size="sm" className='mx-3' onClick={() => { setUserPageState("testVideo"); }}>
                                        Generate Videos
                                    </Button>

                                    <Button variant="primary" size="sm" className='mx-3' onClick={() => { setUserPageState("compareTests"); }}>
                                        Results
                                    </Button>
                                </>
                            }
                        </Col>
                    </Row>
                </div>
        }
    }
    useEffect(() => {
        async function getUser() {
            setLoadingShow(true)

            let res = await fetch(`https://shoevideo.azurewebsites.net/api/get_user_info`, {
                method: "POST",
                body: JSON.stringify({
                    'email': email,

                }),
                headers: {
                    'Content-Type': 'application/json',
                    'Accept': 'application/json',
                    'x-functions-key': token
                },
            })

            let resData = await res.json();
            if (res.ok) {
                setUserType(resData["user_type"])
                setLoadingShow(false)
            }
            else {
                setLoadingShow(false)
            }
        }
        getUser();

    }, [email, token]);

    return (
        <div>
            <LoadingModal show={loadingShow} text={'Getting user data'} />
            {main(userPageState)}
        </div >
    );
};
export default UserPage;
